#categories_button {
  background-color: #3c3c3c;
  width: 180px;
  height: 65px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1% 8%;
  cursor: pointer;
}
#categories_button > p {
  color: white;
  font-weight: 700;
  font-size: 17px;
}
#categories_button:hover {
  opacity: 0.85;
}
